<script>
import Password from '@components/settings/password.vue';
import validate from '@/utils/validate';
import layer from '@/utils/layer';
import api from '@/api';

export default {
  name: 'account',
  components: {
    Password,
  },
  data() {
    return {
      password: '',
      new_password: '',
      password_verify: '',
      errorType: [],
      email: null,
    };
  },
  mounted() {
    this.email = localStorage.getItem('email');
  },
  methods: {
    changeIcon(arr) {
      this.$set(this, 'errorType', arr.type);
      this.new_password = arr.new_password;
    },
    savePassword() {
      const obj = {
        password: this.password,
        new_password: this.new_password,
        password_verify: this.password_verify,
      };
      this.validResult = validate.isRequiredCheck(obj);
      const errorType = this.errorType.filter(i => i > 0);

      if (errorType.length < 4) {
        layer.tip('Please make sure the new password is correct.', 'error');
        return;
      }
      if (!this.validResult.valid) {
        layer.tip(this.validResult.msg, 'error');
      } else {
        api.loginApi.resetpassword(obj).then((result) => {
          if (!result.success) {
            // layer.tip('Please fill in the password information.', 'error');
            const msg = api.errmsg.getErrmsg(result);
            layer.tip(msg, 'error');
          } else {
            layer.tip('Reset password successfully !');
            api.loginApi.loginout();
            // setTimeout(()=>{
            //   // this.$router.push('/loginRegister/login');
            // },1000)
          }
        });
      }
    },
  },

};
</script>

